* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

.history {
    font-family: 'Nunito', sans-serif;
    margin: 40px 20px;
    position: relative;
    display: flex;
    flex-direction: row;
    padding: 20px 30px;
    /* border: 5px outset; */
    width: 85%;
    margin-left: auto;
}

.heading {
    font-size: 35px;
    padding-bottom: 2px;
    text-align: end;
    font-weight: 700;
}

.horizontal_line {
    width: 170px;
    border: #fff;
    border-bottom: 2px solid black;
}

.history_description {
    text-align: justify;
    padding: 5px 0 0 0;
    line-height: 1.6rem;
}

.history_img {
    height: 250px;
    width: 250px;
    border-radius: 10px;
    margin-left: 40px;
}

@media screen and (max-width: 1100px) {
    .history {
        padding: 5px;
        border: none;
        width: 100%;
        margin: 40px 0;
    }

    .heading {
        text-align: left;
    }

    .history_img {
        display: none;
    }

    .history_description {
        padding: 0 10px 0 10px;
    }
}

@media screen and (max-width: 768px) {
    .heading {
        font-size: 30px;
    }
}